body {
  font-family: "Noto Sans", Arial, sans-serif;
  font-size: 14px; }

h1, h2, h3, h4, h5, h6 {
  font-family: "Droid Serif", Georgia, serif;
  font-weight: 400;
  margin: 0;
  padding: 0;
  padding-bottom: 15px; }

h1 {
  font-size: 28px; }

h2 {
  font-size: 24px; }

h3 {
  font-size: 20px; }

h4 {
  font-size: 16px; }

p {
  margin: 0;
  padding: 0;
  padding-bottom: 10px; }

a {
  color: #999;
  text-decoration: none; }
  a:hover {
    text-decoration: underline; }

img {
  max-width: 100%;
  height: auto; }

/* Header Styles */
header {
  padding: 10px 0; }

nav {
  text-align: center; }

.site-navigation {
  font-family: "Droid Serif", Georgia, serif;
  padding: 0;
  display: flex;
  justify-content: center; }
  .site-navigation a {
    color: #333;
    display: block;
    padding: 10px; }
  .site-navigation li {
    flex: none;
    list-style: none;
    margin: 0 25px; }

.site-logo {
  font-family: "Work Sans";
  font-weight: 600;
  text-transform: uppercase;
  font-size: 48px;
  display: block;
  color: #333;
  letter-spacing: 8px;
  text-align: center; }
  .site-logo:hover {
    text-decoration: none; }

.checkout-bag {
  background: url("/img/everlane-bag.svg") no-repeat;
  height: 42px;
  width: 46px;
  float: right;
  display: block;
  text-align: center;
  border: 1px solid transparent; }
  .checkout-bag span {
    position: relative;
    top: 13px; }
  .checkout-bag:hover {
    text-decoration: none;
    border: 1px solid #999;
    border-bottom: #fff;
    position: relative;
    z-index: 3; }

.checkout-cart {
  position: absolute;
  max-width: 400px;
  border: 1px solid #999;
  padding: 15px;
  z-index: 3;
  background-color: #fff;
  top: 40px; }
  .checkout-cart h4 {
    text-align: center; }

.items {
  padding: 0;
  margin: 0; }

.item {
  list-style: none; }
  .item img {
    max-width: 30%;
    float: left; }

.product-detail-overlay {
  position: absolute;
  height: 100%;
  width: 100%;
  z-index: 0;
  transition: .3s ease all;
  top: 0;
  opacity: 0;
  z-index: -1; }
  .product-detail-overlay[data-active="true"] {
    display: block;
    background: rgba(0, 0, 0, 0.3);
    margin: auto;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    z-index: 5;
    opacity: 1;
    cursor: pointer; }
    .product-detail-overlay[data-active="true"] .product-details {
      bottom: 0;
      top: 20%; }

.product-details {
  max-width: 600px;
  position: relative;
  margin: 0 auto;
  padding: 20px;
  background-color: #fff;
  bottom: 100%;
  top: 0;
  transition: .5s ease all; }

/* indclude module styles here or include separate module files from the modules directory */
.product {
  display: block;
  margin-bottom: 15px;
  position: relative; }
  .product:hover .product-overlay {
    opacity: 1;
    z-index: 1;
    text-decoration: none;
    bottom: 0;
    top: 0; }

.product-overlay {
  position: absolute;
  padding: 30px 0;
  max-height: 82px;
  background-color: rgba(255, 255, 255, 0.9);
  top: -20%;
  bottom: 100%;
  left: 0;
  right: 0;
  text-align: center;
  margin: auto;
  font-size: 16px;
  opacity: 0;
  z-index: -1;
  transition: .4s ease all; }

.product-name {
  text-align: center;
  margin-top: 15px; }

